<template>
	<div id="current-detail">
		<public-header title="交易详情"></public-header>
		<div class="content">
			<div class="van-form-preview">
				<div class="van-form-preview__hd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">变动金额</div>
						<div class="van-form-preview__value_in-hd" :class="{'c-primary': record.num > 0, 'c-danger': record.num < 0}">{{record.num}}</div>
					</div>
				</div>
				<div class="van-form-preview__bd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">交易类型</div>
						<div class="van-form-preview__value">{{record.credittype_cn}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">变动时间</div>
						<div class="van-form-preview__value">{{record.createtime_cn}}</div>
					</div>
					<template v-if="record.remark">
						<div class="van-form-preview__item">
							<div class="van-form-preview__label">详细信息</div>
							<div class="van-form-preview__value">{{record.remark}}</div>
						</div>
					</template>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				record: {},
				getcash_log: {}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'wmall/member/credit/detail',
					data: {
						id: that.id
					},
					success: (res) => {
						this.record = that.util.extend(this.record, res.record);
					}
				})
			}
		},
		created() {
			this.query = this.util.parseQuery(this.$route.query);
			this.id = this.query.id;
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#current-detail .van-form-preview {
		position: relative;
		background-color: #fff;
	}
	#current-detail .van-form-preview .van-form-preview__value {
		font-size: 14px
	}
	#current-detail .van-form-preview .van-form-preview__value_in-hd {
		font-size: 26px
	}
	#current-detail .van-form-preview .van-form-preview__hd {
		position: relative;
		padding: 10px 15px;
		text-align: right;
		line-height: 40px;
		overflow: hidden;
	}
	#current-detail .van-form-preview .van-form-preview__hd:after {
		content: '';
		position: absolute;
		top: 0;
		left: 15px;
		width: 200%;
		height: 200%;
		-webkit-transform: scale(.5);
		transform: scale(.5);
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		pointer-events: none;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		border: 0 solid #e5e5e5;
		border-bottom-width: 1px;
	}
	#current-detail .van-form-preview .van-form-preview__bd {
		padding: 10px 15px;
		font-size: 15px;
		text-align: right;
		color: #999;
		line-height: 2
	}
	#current-detail .van-form-preview .van-form-preview__ft {
		position: relative;
		line-height: 50px;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex
	}
	#current-detail .van-form-preview .van-form-preview__ft:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 200%;
		height: 200%;
		-webkit-transform: scale(.5);
		transform: scale(.5);
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		pointer-events: none;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		border: 0 solid #e5e5e5;
		border-top-width: 1px;
	}
	#current-detail .van-form-preview .van-form-preview__item {
		overflow: hidden
	}
	#current-detail .van-form-preview .van-form-preview__label {
		float: left;
		margin-right: 10px;
		min-width: 60px;
		color: #999;
		text-align: justify;
		text-align-last: justify
	}
	#current-detail .van-form-preview .van-form-preview__value {
		display: block;
		overflow: hidden;
		word-break: normal;
		word-wrap: break-word
	}
	#current-detail .van-form-preview .van-form-preview__btn {
		position: relative;
		display: block;
		-webkit-box-flex: 1;
		-webkit-flex: 1;
		flex: 1;
		color: #3cc51f;
		text-align: center
	}
	#current-detail .van-form-preview .van-form-preview__btn:after{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 200%;
		height: 200%;
		-webkit-transform: scale(.5);
		transform: scale(.5);
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		pointer-events: none;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		border: 0 solid #e5e5e5;
		border-left-width: 1px;
	}
	#current-detail .van-form-preview .van-form-preview__btn:first-child:after {
		display: none
	}
	#current-detail .van-form-preview .van-form-preview__btn_active {
		background-color: #eee
	}
	#current-detail .van-form-preview .van-form-preview__btn_default {
		color: #999
	}
	#current-detail .van-form-preview .van-form-preview__btn_primary {
		color: #0bb20c
	}
</style>


